@charset "utf-8";

.todo_item {
  padding: 1rem;
  display: flex;
  /* 세로 가운데 정렬 */
  align-items: center;
  cursor: pointer;
  line-height: 40px;
  position: relative;
}

.todo_item:hover {
  background-color: #ccc;
}

/* .todo_item 사이에 윗 테두리 */
.todo_item+.todo_item {
  border-top: 1px solid #f1f3f5;
}

/* 삭제버튼 서식 X */
.remove {
  /* opacity: 0; */
  font-weight: bold;
  color: #f00;
  margin-right: 0.5rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 1rem;
}

.todo_text {
  flex: 1;
  word-break: break-all;
}

/* 해당 글 목록 클릭시 글자 서식이 흐리고, 가운데 줄 표시하도록 함 */
.checked {
  /* 글자 흐리게 */
  color: #adb5bd;
}

.checked span:not(.check_mark) {
  /* 취소선 서식 */
  text-decoration: line-through;
}

.check_mark {
  font-size: 1.5rem;
  line-height: normal;
  color: #f00;
  font-weight: bold;
}